/* Grid Container */
.grid-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr)); /* Dynamic columns */
    grid-gap: 10px;
    margin: 0 auto;
    padding: 10px;
    max-width: 100%;
  }
  
  /* Grid Items */
  .grid-item {
    background-color: #ecf0f1;
    position: relative;
    padding-top: 100%; /* Square shape */
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .grid-item-content {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  /* Responsive Adjustments */
  
  /* Mobile phones */
  @media (max-width: 600px) {
    .grid-container {
      grid-template-columns: repeat(auto-fit, minmax(80px, 1fr)); /* Smaller squares on mobile */
      grid-gap: 8px;
    }
  }
  
  /* Tablets */
  @media (min-width: 601px) and (max-width: 900px) {
    .grid-container {
      grid-template-columns: repeat(auto-fit, minmax(120px, 1fr)); /* Adjust size for tablets */
    }
  }
  
  /* Larger screens */
  @media (min-width: 901px) {
    .grid-container {
      grid-template-columns: repeat(auto-fit, minmax(150px, 1fr)); /* Slightly larger for desktops */
    }
  }